import axios from "axios";

// API  
const apiBaseURL =
  process.env.REACT_APP_API_BASE_URL || "http://localhost:8080";

const api = axios.create({
  baseURL: apiBaseURL,
  timeout: 10000,
});

// --------------------------
//  
// --------------------------
function getToken() {
  try {
    const auth =
      localStorage.getItem("auth") || sessionStorage.getItem("auth");

    if (auth) {
      const parsed = JSON.parse(auth);
      if (parsed?.token) return parsed.token;
    }

    return (
      localStorage.getItem("token") ||
      sessionStorage.getItem("token") ||
      null
    );
  } catch {
    return null;
  }
}

// --------------------------
//  
// --------------------------
api.interceptors.request.use((config) => {
  const token = getToken();
  if (token) {
    config.headers = config.headers || {};
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// --------------------------
//  
// --------------------------
api.interceptors.response.use(
  (res) => {
    const body = res.data;

    //  { resultCode, resultMsg, data }    
    if (body && typeof body === "object" && "resultCode" in body) {
      if (body.resultCode === 200) return body;

      const err = new Error(body.resultMsg || " ");
      err.body = body;
      throw err;
    }

    return res;
  },
  (err) => {
    const msg =
      err?.response?.data?.resultMsg ||
      err?.response?.data?.message ||
      err.message ||
      " ";

    const wrapped = new Error(msg);
    wrapped.original = err;
    return Promise.reject(wrapped);
  }
);

// -------------------------
//   API
// -------------------------

// 
// payload: { userEmail, userPw, asAdmin(false ) }
export const login = (payload) =>
  api.post("/api/auth/login", payload);

// 
export const signUp = (payload) =>
  api.post("/api/auth/signup", payload);

//   
export const requestPhoneVerify = (payload) =>
  api.post("/api/auth/phone/request", payload);

//   
export const confirmPhoneVerify = (payload) =>
  api.post("/api/auth/phone/verify", payload);

// -------------------------
//   URL ( )
// -------------------------
export function getKakaoUrl() {
  const base = apiBaseURL.replace(/\/+$/, ""); //  / 
  //  /auth/kakao/login  ,   redirect 
  return `${base}/auth/kakao/login`;
}

export default api;
