import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';

const ProductDetail = () => {
  const { prdNo } = useParams();
  const navigate = useNavigate();

  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);

  const apiBaseUrl = 'http://localhost:8085/api/admin/products';

  //   
  useEffect(() => {
    const loadProduct = async () => {
      try {
        const result = await axios.get(`${apiBaseUrl}/${prdNo}`);
        setProduct(result.data);
      } catch (err) {
        console.error('   :', err);
        alert('    .');
      }
      setLoading(false);
    };

    loadProduct();
  }, [prdNo]);

  //  
  const handleDelete = async () => {
    const confirmDelete = window.confirm('   ?');
    if (!confirmDelete) return;

    try {
      await axios.delete(`${apiBaseUrl}/${prdNo}`);
      alert(' .');
      navigate('/admin/productList');
    } catch (err) {
      console.error(err);
      alert('    .');
    }
  };

  if (loading) return <div className="text-center mt-5">  ...</div>;
  if (!product) return <div className="text-center mt-5">    .</div>;

  return (
    <div className="container mt-4" style={{ maxWidth: '800px' }}>
      <h2 className="mb-4 text-center">   </h2>

      <div className="card shadow-sm">
        <div className="card-body">
          {/*   */}
          <div className="text-center mb-4">
            <img
              src={`/images/${product.prdImg}`}
              alt={product.prdName}
              style={{
                width: '200px',
                height: '200px',
                objectFit: 'cover',
                borderRadius: '12px',
                border: '1px solid #ddd',
              }}
            />
          </div>

          {/*    */}
          <table className="table table-bordered">
            <tbody>
              <tr>
                <th style={{ width: '150px' }}> </th>
                <td>{product.prdNo}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdName}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdCompany}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdPrice?.toLocaleString()}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdVolume}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdMainCate}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdSubCate}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdStock}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdStatus || ''}</td>
              </tr>
              <tr>
                <th></th>
                <td>{product.prdDesc}</td>
              </tr>
            </tbody>
          </table>

          {/*   */}
          <div className="text-center mt-4">
            <button className="btn btn-secondary me-2" onClick={() => navigate('/admin/productList')}>
               
            </button>

            <button className="btn btn-primary me-2" onClick={() => navigate(`/admin/products/edit/${prdNo}`)}>
               
            </button>

            <button className="btn btn-danger" onClick={handleDelete}>
               
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductDetail;
