import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';

const ProductEdit = () => {
  const { prdNo } = useParams();
  const navigate = useNavigate();

  const apiBaseUrl = 'http://localhost:8085/api/admin/products';

  const [product, setProduct] = useState({
    prdName: '',
    prdPrice: '',
    prdVolume: '',
    prdCompany: '',
    prdMainCate: '',
    prdSubCate: '',
    prdImg: '',
    prdDesc: '',
    prdStock: '',
    prdStatus: '',
  });

  const [previewImg, setPreviewImg] = useState('');

  //    
  useEffect(() => {
    axios
      .get(`${apiBaseUrl}/${prdNo}`)
      .then(res => {
        setProduct(res.data);
        setPreviewImg(`/images/${res.data.prdImg}`);
      })
      .catch(() => alert('    .'));
  }, [prdNo]);

  //  
  const handleChange = e => {
    setProduct({ ...product, [e.target.name]: e.target.value });
  };

  //    
  const handleImageChange = e => {
    const file = e.target.files[0];
    if (!file) return;

    setProduct({ ...product, prdImg: file });

    // preview
    const reader = new FileReader();
    reader.onload = () => setPreviewImg(reader.result);
    reader.readAsDataURL(file);
  };

  //  
  const handleSave = async () => {
    try {
      //    multipart/form-data
      const formData = new FormData();
      Object.keys(product).forEach(key => {
        if (key !== 'prdRegdate' && key !== 'prdUpdate') {
          formData.append(key, product[key]);
        }
      });

      await axios.put(`${apiBaseUrl}/${prdNo}`, formData, {
        headers: { 'Content-Type': 'multipart/form-data' },
      });

      alert('  !');
      navigate(`/admin/products/detail/${prdNo}`);
    } catch (err) {
      console.error(err);
      alert('  .');
    }
  };

  return (
    <div className="container mt-4" style={{ maxWidth: '800px' }}>
      <h2 className="mb-4 text-center">  </h2>

      <div className="card shadow-sm">
        <div className="card-body">
          {/*   */}
          <div className="text-center mb-4">
            <img
              src={previewImg}
              alt=" "
              style={{
                width: '200px',
                height: '200px',
                objectFit: 'cover',
                borderRadius: '12px',
                border: '1px solid #ddd',
              }}
            />
            <div className="mt-2">
              <input type="file" onChange={handleImageChange} />
            </div>
          </div>

          {/*   */}
          <table className="table table-bordered">
            <tbody>
              <tr>
                <th style={{ width: '150px' }}></th>
                <td>
                  <input
                    type="text"
                    name="prdName"
                    className="form-control"
                    value={product.prdName}
                    onChange={handleChange}
                  />
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <input
                    type="text"
                    name="prdCompany"
                    className="form-control"
                    value={product.prdCompany}
                    onChange={handleChange}
                  />
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <input
                    type="number"
                    name="prdPrice"
                    className="form-control"
                    value={product.prdPrice}
                    onChange={handleChange}
                  />
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <input
                    type="text"
                    name="prdVolume"
                    className="form-control"
                    value={product.prdVolume}
                    onChange={handleChange}
                  />
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <input
                    type="text"
                    name="prdMainCate"
                    className="form-control"
                    value={product.prdMainCate}
                    onChange={handleChange}
                  />
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <input
                    type="text"
                    name="prdSubCate"
                    className="form-control"
                    value={product.prdSubCate}
                    onChange={handleChange}
                  />
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <input
                    type="number"
                    name="prdStock"
                    className="form-control"
                    value={product.prdStock}
                    onChange={handleChange}
                  />
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <select name="prdStatus" className="form-select" value={product.prdStatus} onChange={handleChange}>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                </td>
              </tr>

              <tr>
                <th></th>
                <td>
                  <textarea
                    name="prdDesc"
                    className="form-control"
                    rows="4"
                    value={product.prdDesc}
                    onChange={handleChange}
                  ></textarea>
                </td>
              </tr>
            </tbody>
          </table>

          {/*   */}
          <div className="text-center mt-4">
            <button className="btn btn-secondary me-2" onClick={() => navigate(-1)}>
               
            </button>

            <button className="btn btn-primary" onClick={handleSave}>
               
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductEdit;
