import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

const API_BASE_URL = 'http://localhost:8080/api';

// createAsyncThunk ,   
//  Thunk  pending, fulfilled, rejected  

/**
 *     (GET /api/cart)
 *         
 *
 * :     
 * - items:   
 * - summary:     
 */
export const fetchCartView = createAsyncThunk('cart/fetchCartView', async (_, { rejectWithValue }) => {
  try {
    const response = await fetch(`${API_BASE_URL}/cart`);
    if (!response.ok) throw new Error('  ');
    return await response.json();
  } catch (e) {
    return rejectWithValue(e.message);
  }
});

/**
 *     (PATCH /api/cart/items/:cartItemId)
 *
 * @param {number} cartItemId -     ID
 * @param {number} quantity -   (0  )
 * @returns {Object} { cartItemId, quantity } -    
 */
export const updateCartItemQuantity = createAsyncThunk(
  'cart/updateItemQuantity',
  async ({ cartItemId, quantity }, { rejectWithValue }) => {
    try {
      const response = await fetch(`${API_BASE_URL}/cart/items/${cartItemId}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ quantity }),
      });
      if (!response.ok) throw new Error('  ');

      //  (response.json())    
      //    state  state   
      return { cartItemId, quantity };
    } catch (e) {
      return rejectWithValue(e.message);
    }
  },
);

/**
 *     (DELETE /api/cart/items)
 *
 * @param {number[]} cartItemIds -    ID 
 * @returns {number[]} cartItemIds -  ID 
 */
export const deleteCartItems = createAsyncThunk('cart/deleteItems', async (cartItemIds, { rejectWithValue }) => {
  try {
    const response = await fetch(`${API_BASE_URL}/cart/items`, {
      method: 'DELETE',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ cartItemIds }),
    });
    if (!response.ok) throw new Error('  ');

    //     ID    
    return cartItemIds;
  } catch (e) {
    return rejectWithValue(e.message);
  }
});

// Redux State 

/**
 *  State 
 *
 * items:    (   )
 * summary:      (  )
 * selectedItemIds:   ID    ( )
 *   - : { 1: true, 3: true }  ID 1, 3  
 *   - API    
 * loading: API   ('idle' | 'pending' | 'succeeded' | 'failed')
 *   - : fetchCartView 'pending'    UI 
 *   - updateCartItemQuantity, deleteCartItems      
 * error:  
 */
const initialState = {
  items: [],
  summary: {
    deliveryFee: 3000, //   
  },
  selectedItemIds: {}, //    ( )
  loading: 'idle',
  error: null,
};

/**
 * fetchCartView    
 *
 *  :
 * 1.      state 
 * 2. summary 
 * 3.      
 *
 *  :      
 */
const handleFetchSuccess = (state, action) => {
  if (action.payload && action.payload.resultCode === 200 && action.payload.data) {
    state.items = action.payload.data.items;
    state.summary = action.payload.data.summary;
    state.loading = 'succeeded';

    // reduce   cartItemId    
    state.selectedItemIds = state.items.reduce((acc, item) => {
      acc[item.cartItemId] = true;
      return acc;
    }, {});
  } else {
    state.loading = 'failed';
    state.error = action.payload ? action.payload.resultMsg : 'API   .';
  }
};

// Redux Slice 

const cartSlice = createSlice({
  name: 'cart',
  initialState,

  //  Reducer: API    state 
  // /      
  reducers: {
    /**
     *   / 
     * @param {number} action.payload - cartItemId
     */
    toggleItemSelection: (state, action) => {
      const cartItemId = action.payload;
      if (state.selectedItemIds[cartItemId]) {
        //       ( )
        delete state.selectedItemIds[cartItemId];
      } else {
        //      ( )
        state.selectedItemIds[cartItemId] = true;
      }
    },

    /**
     *  /
     *
     * @param {boolean} action.payload - true:  , false:  
     */
    toggleAllSelection: (state, action) => {
      const shouldSelectAll = action.payload;
      state.selectedItemIds = shouldSelectAll
        ? state.items.reduce((acc, item) => {
            acc[item.cartItemId] = true;
            return acc;
          }, {})
        : {}; //     
    },
  },
  //  Thunk  (extraReducers)
  // createAsyncThunk  pending, fulfilled, rejected  

  extraReducers: builder => {
    builder
      /**
       * [fetchCartView.fulfilled]   
       *
       *     state 
       *        
       */
      .addCase(fetchCartView.fulfilled, handleFetchSuccess)

      /**
       * [updateCartItemQuantity.fulfilled]   API 
       *
       *  :
       * 1. quantity === 0    
       * 2. quantity >= 1     
       */
      .addCase(updateCartItemQuantity.fulfilled, (state, action) => {
        const { cartItemId, quantity } = action.payload;

        if (quantity === 0) {
          //  0   
          state.items = state.items.filter(item => item.cartItemId !== cartItemId);
          //    
          delete state.selectedItemIds[cartItemId];
        } else {
          //  1     
          const itemToUpdate = state.items.find(item => item.cartItemId === cartItemId);
          if (itemToUpdate) {
            // Redux Toolkit Immer    
            itemToUpdate.quantity = quantity;
          }
        }

        // 'succeeded'   (loading UI   )
        state.loading = 'succeeded';
      })

      /**
       * [deleteCartItems.fulfilled]   API 
       *
       *  :
       * 1. items   ID 
       * 2. selectedItemIds  ID 
       *
       *  : Set   O(1) 
       */
      .addCase(deleteCartItems.fulfilled, (state, action) => {
        const deletedIds = new Set(action.payload); //  Set 

        // items    
        state.items = state.items.filter(item => !deletedIds.has(item.cartItemId));

        //     
        deletedIds.forEach(id => {
          delete state.selectedItemIds[id];
        });

        state.loading = 'succeeded';
      })

      /**
       * [fetchCartView.pending]    
       *
       * : fetchCartView loading = 'pending' 
       * - updateCartItemQuantity, deleteCartItems pending  
       * -    /   UI  
       */
      .addCase(fetchCartView.pending, state => {
        state.loading = 'pending';
      })

      /**
       * [ Thunk  ]
       * addMatcher   '/rejected'    
       * - fetchCartView, updateCartItemQuantity, deleteCartItems  
       * -   state  UI 
       */
      .addMatcher(
        action => action.type.endsWith('/rejected'),
        (state, action) => {
          state.loading = 'failed';
          state.error = action.payload;
        },
      );
  },
});

export const { toggleItemSelection, toggleAllSelection } = cartSlice.actions;

export default cartSlice.reducer;
