/**
 *    
 * -      ( , ,   )
 * -   (   )
 * -   
 * - :   
 * - :   
 *
 * Props:
 * - summary:    { totalAmount, deliveryFee, finalPaymentAmount }
 * - selectedCount:   
 * - onNavigate:  
 *
 *  :
 * - summary CartPage   (  )
 * -   ( /  )
 */

import React from 'react';
import PropTypes from 'prop-types';
import './CartPage.css';

export function CartSummary({ summary, selectedCount, onNavigate }) {
  //    
  const deliveryFee = summary.deliveryFee;
  const finalAmount = summary.finalPaymentAmount;
  const totalAmount = summary.totalAmount;

  //  
  const CheckoutButton = () => (
    <button
      className="btn btn-primary btn-pill btn-lg"
      disabled={selectedCount === 0}
      onClick={() => onNavigate('/checkout')}
    >
       
    </button>
  );

  return (
    <>
      {/* :    */}
      <aside className="order-summary-sidebar">
        <div className="order-summary-sticky">
          <div className="card shadow-lg">
            <div className="card-content">
              <h3 className="mb-4"> </h3>

              {/*    */}
              <div className="summary-details">
                {/*   ( ) */}
                <div className="summary-row">
                  <span className="text-muted"> </span>
                  <span>{totalAmount.toLocaleString()}</span>
                </div>

                {/*  */}
                <div className="summary-row">
                  <span className="text-muted"></span>
                  <span>{deliveryFee === 0 ? '' : `${deliveryFee.toLocaleString()}`}</span>
                </div>

                {/*     */}
                {deliveryFee > 0 && (
                  <p className="alert-message">{(30000 - totalAmount).toLocaleString()}   !</p>
                )}

                <hr className="hr-separator" />

                {/*    */}
                <div className="summary-row summary-total">
                  <span>  </span>
                  <span className="total-price">{finalAmount.toLocaleString()}</span>
                </div>
              </div>

              {/*   */}
              <div className="summary-buttons">
                <CheckoutButton />
                <button className="btn btn-outline btn-pill" onClick={() => onNavigate('/')}>
                   
                </button>
              </div>

              {/*   */}
              <div className="summary-info">
                <p> 30,000    </p>
              </div>
            </div>
          </div>
        </div>
      </aside>

      {/* :    */}
      <div className="mobile-summary-footer">
        <div className="mobile-summary-content">
          <div className="mobile-price-section">
            <p className="text-muted">  </p>
            <p className="total-price">{finalAmount.toLocaleString()}</p>
          </div>
          <CheckoutButton />
        </div>
        {/*   () */}
        {deliveryFee > 0 && (
          <p className="alert-message">{(30000 - totalAmount).toLocaleString()}   !</p>
        )}
      </div>
    </>
  );
}

CartSummary.propTypes = {
  summary: PropTypes.shape({
    totalAmount: PropTypes.number.isRequired,
    deliveryFee: PropTypes.number.isRequired,
    finalPaymentAmount: PropTypes.number.isRequired,
  }).isRequired,
  selectedCount: PropTypes.number.isRequired,
  onNavigate: PropTypes.func.isRequired,
};
