// src/App.js-
import React, { useState } from 'react';
import { Routes, Route, useNavigate, useLocation, Navigate } from 'react-router-dom';
import { CartPage } from './pages/CartPage';

import UserGlobalLayout from './components/user/layouts/UserGlobalLayout';

// TODO: / API   ENDPOINTS import  .
// import { Header, ENDPOINTS as HEADER_ENDPOINTS } from './components/user/layouts/Header';
import Home from 'pages/Home';
import MyPage from './pages/MyPage';
import ProductDetailPage from './pages/ProductDetailPage';
import LoginPage from './pages/LoginPage';
import SignupPage from './pages/SignupPage';
import LogoutPage from './pages/LogoutPage';
import AdminHome from 'pages/admin/AdminHome';
import Footer from 'components/user/layouts/footer';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userRole, setUserRole] = useState('user');
  const navigate = useNavigate();
  const location = useLocation();
  const isAdmin = location.pathname.startsWith('/admin');

  //    route key   /      
  const handleLoginClick = () => {
    //       
    navigate('/login');
  };

  const handleSignupClick = () => {
    //   
    navigate('/signup');
  };

  const handleLogoutClick = () => {
    //    /    
    setIsLoggedIn(false);
    navigate('/logout');
  };

  const handleRequireLogin = () => {
    navigate('/login');
  };

  const handleCartNavigate = () => {
    navigate('/cart');
  };

  const handleNavigate = page => {
    const routes = {
      home: '/',
      admin: '/admin',
      mypage: '/mypage',
      cart: '/cart',
      order: '/order',
      customerservice: '/customerservice',
      category: '/category',
      ranking: '/ranking',
      product: '/product',
    };

    if (routes[page]) {
      navigate(routes[page]);
    }
  };

  return (
    <div className="App">
      {/*    ,        */}
      {!isAdmin && (
        <UserGlobalLayout
          isLoggedIn={isLoggedIn}
          userRole={userRole}
          onLoginChange={setIsLoggedIn}
          onLoginClick={handleLoginClick}
          onSignupClick={handleSignupClick}
          onLogoutClick={handleLogoutClick}
          onNavigate={handleNavigate}
          onRoleChange={setUserRole}
          onRequireLogin={handleRequireLogin}
          onCartClick={handleCartNavigate}
        />
      )}

      {/*   */}
      <main className="app-body" style={{ paddingTop: !isAdmin ? '140px' : '0px' }}>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/cart" element={<CartPage />} />

          {/* TODO:    (   ).       Route   */}
          {/* <Route
            path="/mypage"
            element={isLoggedIn ? <MyPage /> : <Navigate to="/login" replace />}
          /> */}
          <Route path="/mypage" element={<MyPage />} />

          <Route path="/product/:id" element={<ProductDetailPage />} />
          <Route path="/admin/*" element={<AdminHome />} />

          {/*  */}
          <Route path="/login" element={isLoggedIn ? <Navigate to="/" replace /> : <LoginPage />} />

          {/*  */}
          <Route path="/signup" element={isLoggedIn ? <Navigate to="/" replace /> : <SignupPage />} />

          {/*  */}
          <Route path="/logout" element={<LogoutPage />} />

          {/* 404 */}
          <Route path="*" element={<div style={{ padding: 24 }}>   .</div>} />
        </Routes>
      </main>

      {/*           */}
      {!isAdmin && <Footer />}
    </div>
  );
}

export default App;
