import React, { useState, useEffect, useRef } from 'react';
import { useDispatch } from 'react-redux';
import { X, Lock, Check, AlertCircle } from 'lucide-react';
import { changePassword } from '../../../features/user/userSlice';
import '../../../styles/MyPage.css'; // Reusing MyPage styles for consistency

/**
 * PasswordChange Component
 *
 *     .
 *  ,  ,      
 * Redux      .
 */
const PasswordChange = ({ isOpen, onClose }) => {
  const dispatch = useDispatch();

  // Form State:    
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  // UI State:     
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // Validation State:     
  const [hasUpperCase, setHasUpperCase] = useState(false); //   
  const [hasSpecialChar, setHasSpecialChar] = useState(false); //   

  // Refs:           
  const currentPasswordRef = useRef(null);
  const newPasswordRef = useRef(null);
  const confirmPasswordRef = useRef(null);

  // Effect:            
  useEffect(() => {
    if (!isOpen) {
      setCurrentPassword('');
      setNewPassword('');
      setConfirmPassword('');
      setError('');
      setSuccess('');
      setHasUpperCase(false);
      setHasSpecialChar(false);
    }
  }, [isOpen]);

  if (!isOpen) return null;

  // Handler:        
  const handleNewPasswordChange = e => {
    const val = e.target.value;
    setNewPassword(val);
    //    
    setHasUpperCase(/[A-Z]/.test(val));
    //    
    setHasSpecialChar(/[!@#$%^&*(),.?":{}|<>]/.test(val));
  };

  // Handler:      
  const handleSubmit = e => {
    e.preventDefault();
    setError('');
    setSuccess('');

    // 1.   
    if (!currentPassword) {
      setError('  .');
      currentPasswordRef.current?.focus();
      return;
    }

    if (!newPassword) {
      setError('  .');
      newPasswordRef.current?.focus();
      return;
    }

    if (!confirmPassword) {
      setError('   .');
      confirmPasswordRef.current?.focus();
      return;
    }

    // 2.    
    if (newPassword !== confirmPassword) {
      setError('   .');
      confirmPasswordRef.current?.focus();
      return;
    }

    // 3.  
    if (newPassword.length < 8) {
      setError(' 8  .');
      newPasswordRef.current?.focus();
      return;
    }

    // 4.    (, )
    if (!hasUpperCase || !hasSpecialChar) {
      setError('    .');
      newPasswordRef.current?.focus();
      return;
    }

    // 5. Redux Action Dispatch:   
    dispatch(changePassword({ currentPassword, newPassword }))
      .unwrap()
      .then(() => {
        setSuccess('  .');
        //     
        setCurrentPassword('');
        setNewPassword('');
        setConfirmPassword('');
        setHasUpperCase(false);
        setHasSpecialChar(false);

        // 1.5    
        setTimeout(() => {
          onClose();
          setSuccess('');
        }, 1500);
      })
      .catch(err => {
        setError(typeof err === 'string' ? err : '  .');
      });
  };

  // Helper:     CSS   ( )
  const getInputClass = (value, isValid = true) => {
    if (!value) return '';
    return isValid ? 'valid-input' : 'invalid-input';
  };

  return (
    <div className="profile-modal-overlay">
      <div className="profile-modal-container password-modal">
        {/* Modal Header */}
        <div className="modal-header">
          <div className="header-left">
            <div className="header-title-row">
              <Lock size={24} className="header-icon" />
              <h2> </h2>
            </div>
            <span className="header-desc">      .</span>
          </div>
          <button className="modal-close-btn" onClick={onClose}>
            <X size={24} color="#666" />
          </button>
        </div>

        {/* Modal Body */}
        <div className="modal-body">
          <form onSubmit={handleSubmit} className="password-form-modal">
            {/*    */}
            <div className="form-group">
              <label htmlFor="currentPassword"> </label>
              <div className="input-wrapper">
                <input
                  ref={currentPasswordRef}
                  type="password"
                  id="currentPassword"
                  value={currentPassword}
                  onChange={e => setCurrentPassword(e.target.value)}
                  placeholder="  "
                  className={getInputClass(currentPassword)}
                />
              </div>
            </div>

            {/*       */}
            <div className="form-group">
              <label htmlFor="newPassword"> </label>
              <div className="input-wrapper">
                <input
                  ref={newPasswordRef}
                  type="password"
                  id="newPassword"
                  value={newPassword}
                  onChange={handleNewPasswordChange}
                  placeholder="  (8 )"
                  className={getInputClass(newPassword, hasUpperCase && hasSpecialChar && newPassword.length >= 8)}
                />
              </div>
              {/*    (O/X) */}
              <div className="password-rules">
                <span className={hasUpperCase ? 'valid' : 'invalid'}>
                  {hasUpperCase ? <Check size={14} /> : <X size={14} />}  
                </span>
                <span className={hasSpecialChar ? 'valid' : 'invalid'}>
                  {hasSpecialChar ? <Check size={14} /> : <X size={14} />}  
                </span>
              </div>
            </div>

            {/*         */}
            <div className="form-group">
              <label htmlFor="confirmPassword">  </label>
              <div className="input-wrapper">
                <input
                  ref={confirmPasswordRef}
                  type="password"
                  id="confirmPassword"
                  value={confirmPassword}
                  onChange={e => setConfirmPassword(e.target.value)}
                  placeholder="   "
                  className={getInputClass(
                    confirmPassword,
                    confirmPassword === newPassword && confirmPassword.length > 0,
                  )}
                />
              </div>
              <div className="password-rules">
                <span
                  className={newPassword && confirmPassword && newPassword === confirmPassword ? 'valid' : 'invalid'}
                >
                  {newPassword && confirmPassword && newPassword === confirmPassword ? (
                    <Check size={14} />
                  ) : (
                    <X size={14} />
                  )}{' '}
                   
                </span>
              </div>
            </div>

            {/*      */}
            {error && (
              <div className="message-box error">
                <AlertCircle size={16} />
                <span>{error}</span>
              </div>
            )}
            {success && (
              <div className="message-box success">
                <Check size={16} />
                <span>{success}</span>
              </div>
            )}
          </form>
        </div>

        {/* Modal Footer */}
        <div className="modal-footer">
          <button className="cancel-btn" onClick={onClose}>
            
          </button>
          <button className="save-btn" onClick={handleSubmit}>
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default PasswordChange;
