import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { selectItem, clearSelectedItem } from 'features/orders/admOrdersSlice';

import LoadingSpinner from 'components/common/LoadingSpinner';

const OrderDetail = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const apiBaseUrl = useSelector(state => state.admConfig.apiBaseUrl);

  const { odNo } = useParams();
  const itemList = useSelector(state => state.admOrders.list);
  const selectedItem = useSelector(state => state.admOrders.selectedItem);

  const handleBack = () => {
    navigate(-1);
  };
  const getDeliveryKeyText = () => {
    if (selectedItem.ODDELVKEYTYPE === 2) return '';
    if (selectedItem.ODDELVKEYTYPE === 1 && selectedItem.ODDELVKEY === null) return '';
    return `: ${selectedItem.ODDELVKEY}`;
  };

  useEffect(() => {
    const loadOrder = async () => {
      dispatch(clearSelectedItem());
      const existence = itemList.find(e => e.ODNO === parseInt(odNo));
      setLoading(true);
      try {
        if (existence) {
          console.log(existence);
          dispatch(selectItem(existence));
          return;
        } else {
          console.log(`${apiBaseUrl}/orders/detail/${odNo}`);
          const response = await axios.get(`${apiBaseUrl}/orders/detail/${odNo}`);
          dispatch(selectItem(response.data.data));
        }
      } catch (err) {
        console.error('  : ', err);
        setError('   .');
      } finally {
        setLoading(false);
      }
    };
    loadOrder();
  }, [odNo, itemList, dispatch, apiBaseUrl]);

  if (loading) return <LoadingSpinner message="   ..." />;
  if (error) return <div className="text-danger text-center my-5">{error}</div>;
  if (!selectedItem) return <div className="text-center my-5">     .</div>;

  return (
    <div>
      <fieldset>
        <legend className="mb-3">   </legend>
        <h5 className="fw-bold border-bottom pb-2 mb-3 text-primary"> </h5>
        <table className="table table-hover table-bordered align-middle">
          <colgroup>
            <col style={{ width: '20%' }} />
            <col style={{ width: '30%' }} />
            <col style={{ width: '20%' }} />
            <col style={{ width: '30%' }} />
          </colgroup>
          <tbody>
            <tr>
              <th className="bg-light"></th>
              <td>{selectedItem.ODNO}</td>
              <th className="bg-light"></th>
              <td>{selectedItem.ODREGDATE}</td>
            </tr>
            <tr>
              <th className="bg-light"> / </th>
              <td>
                {selectedItem.ODPRDPRICE}  / {selectedItem.ODDELVPRICE} 
              </td>
              <th className="bg-light"> </th>
              <td>{selectedItem.ODPRDPRICE + selectedItem.ODDELVPRICE} </td>
            </tr>
            <tr>
              <th className="bg-light"> (ID) / </th>
              <td colSpan="3">
                {selectedItem.USERNAME}({selectedItem.USERID}) / {selectedItem.USERNICK}
              </td>
            </tr>
            <tr>
              <th className="bg-light"> </th>
              <td colSpan="3">{selectedItem.USERHP}</td>
            </tr>
          </tbody>
        </table>
        <h5 className="fw-bold border-bottom pb-2 mb-3 text-primary"> </h5>
        <table className="table table-hover table-bordered align-middle">
          <colgroup>
            <col style={{ width: '20%' }} />
            <col style={{ width: '30%' }} />
            <col style={{ width: '20%' }} />
            <col style={{ width: '30%' }} />
          </colgroup>
          <tbody>
            <tr>
              <th className="bg-light"></th>
              <td>{selectedItem.ODNAME}</td>
              <th className="bg-light"> </th>
              <td>{selectedItem.ODHP}</td>
            </tr>
            <tr>
              <th className="bg-light"> </th>
              <td colSpan="3">
                {selectedItem.ODJIBUNADDR
                  ? `(${selectedItem.ODZIP}) ${selectedItem.ODJIBUNADDR}, ${selectedItem.ODDETAILADDR}`
                  : ''}
              </td>
            </tr>
            <tr>
              <th className="bg-light"> </th>
              <td colSpan="3">
                {selectedItem.ODROADADDR
                  ? `(${selectedItem.ODZIP}) ${selectedItem.ODROADADDR}, ${selectedItem.ODDETAILADDR}`
                  : ''}
              </td>
            </tr>
            <tr>
              <th className="bg-light"> </th>
              <td>{getDeliveryKeyText()}</td>
              <th className="bg-light"> </th>
              <td>{selectedItem.ODDELVMSG}</td>
            </tr>
          </tbody>
        </table>
      </fieldset>
      <div className="text-center mt-4">
        <button className="btn btn-outline-secondary px-4" onClick={handleBack}>
           
        </button>
      </div>
    </div>
  );
};

export default OrderDetail;
