import React, { useState } from 'react';
import './ProductImageGallery.css';

//images props 
function ProductImageGallery({ images }) {
  //    , 0  
  const [selectedIndex, setSelectedIndex] = useState(0);

  //   ,   index
  const handleThumbnailClick = index => {
    // state  index ,  
    setSelectedIndex(index);
  };

  //  (undefined) ! true ,     ,   (0)
  if (!images || !images.gallery || images.gallery.length === 0) {
    return <div> .</div>;
  }

  return (
    <div className="gallery-container">
      {/* ,  0, selectedIndex   */}
      <img src={images.gallery[selectedIndex]} alt="  " className="main-image" />

      {/*   */}
      <div className="thumbnail-list">
        {/* images.gallery  .map() , imageSrc( ),index() */}
        {images.gallery.map((imageSrc, index) => (
          <div
            //   key
            key={index}
            //     active  (css )
            className={`thumbnail ${selectedIndex === index ? 'active' : ''}`}
            //  handleThumbnailClick 
            onClick={() => handleThumbnailClick(index)}
          >
            {/*   ,  . */}
            <img src={imageSrc} alt={`  ${index + 1}`} />
          </div>
        ))}
      </div>
    </div>
  );
}

export default ProductImageGallery;
